.reel{
    position: relative;
    height: 400px;
    width: 100%;
    overflow: hidden;
}
.imgs{
    width: 100%;
    height: 400px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    overflow: hidden;
}
.imgs img{
    height: 400px;
    width: auto;
    -ms-flex: none;
        flex: none;
}
.left, .right{
    position: absolute;
    display: -ms-flexbox;
    display: flex;
    height: 400px;
    background-color: rgba(0, 0, 0, 0.9); 
    -webkit-backdrop-filter: saturate(0.3); 
            backdrop-filter: saturate(0.3);
    z-index: 3;
    -webkit-transition: cubic-bezier(0.075, 0.82, 0.165, 1) 0.5s width;
    -o-transition: cubic-bezier(0.075, 0.82, 0.165, 1) 0.5s width;
    transition: cubic-bezier(0.075, 0.82, 0.165, 1) 0.5s width;
}
.left{
    left: 0;
    -ms-flex-pack: start;
        justify-content: flex-start;
}
.right{
    right: 0;
    bottom: 0;
    -ms-flex-pack: end;
        justify-content: flex-end;
}

.lilSquare{
    width: 50px;
    height: 100px;
    -ms-flex-item-align: center;
        align-self: center;
    background-color: transparent;
    -webkit-transition: background-color 0.3s ease-in;
    -o-transition: background-color 0.3s ease-in;
    transition: background-color 0.3s ease-in;
}

.lilSquare:hover{
    background-color: rgba(0, 0, 0, 0.9);
}

.left .lilSquare{
    border-top-right-radius: 5px;
    border-bottom-right-radius: 5px;
    border-top: 1px solid #999;
    border-right: 1px solid #999;
    border-bottom: 1px solid #999;
}

.right .lilSquare{
    border-top-left-radius: 5px;
    border-bottom-left-radius: 5px;
    border-top: 1px solid #999;
    border-left: 1px solid #999;
    border-bottom: 1px solid #999;
}

.containArrow{
    height: 100%;
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    justify-items: center; 
}

.arrowLeft{
    width: 0; 
    height: 0; 
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent; 
    border-right:10px solid white;
    margin-left: 17.5px;
}

.arrowRight{
  width: 0; 
  height: 0; 
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;
  border-left: 10px solid white;
  margin-left: 22.5px;
}

@media (max-width: 800px){
    .left, .right{
        display: none;
    }
    .imgs img{
        width: 100%;
        -o-object-fit: cover;
           object-fit: cover;
    }
}


